html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

body {
  background: -webkit-gradient(linear, left bottom, left top, from(#0fb8ad), color-stop(51%, #1fc8db), color-stop(75%, #2cb5e8));
  background: -webkit-linear-gradient(bottom, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
  background: -o-linear-gradient(bottom, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
  background: linear-gradient(0deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
  color: white;
  font-family: "Roboto", sans-serif;
}

.container {
  padding: 10px;
  text-align: center;
}

.container .value {
  font-size: 2rem;
  font-weight: bold;
}

.container button {
  color: white;
  border: 1px solid white;
  border-radius: 5px;
  padding: 10px;
  background: transparent;
  margin: 5px;
  -webkit-transition: all 200ms linear;
  -o-transition: all 200ms linear;
  transition: all 200ms linear;
}

.container button:hover {
  color: black;
  border-color: black;
  background: white;
}

.main-router a {
 color: white;
}


